<template>
	<view>
		<view class="mian_are mian_are_bg">
			<view class="title">手机号验证登录</view>
			<view>
				<view class="msm_mt"><input class="uni-input " type="number" focus v-model="phone" placeholder="请输入手机号码" /></view>
				<view class="msm_line msm_mt">
					<input class="uni-input " focus type="number" v-model="sms" placeholder="请输入验证码" />
					<van-button color="#3E3EF7" block round :disabled="ifsend" @click="send_code" size="small" plain
						hairline>{{msg_msg}}</van-button>
				</view>
			</view>
		</view>
		<view class="mian_are">
			<van-button color="#3E3EF7" block round @click="login" :disabled="ifopen">登录</van-button>
			<view class="text_3">登录即表示已阅读并同意<text class="text_2" @click="To_xieyi">《用户协议》</text><text class="text_2"
					@click="To_yinshi">《隐私权政策》</text></view>
		</view>
		<van-toast id="van-toast"></van-toast>
	</view>
</template>

<script>
	import Toast from '@/wxcomponents/vant/dist/toast/toast';
	export default {
		data() {
			return {
				ifopen: true,
				ifsend: true,
				sms: '',
				phone: '',
				msg_msg: '发送验证码',
				countDownT: 60,
				userInfo:{},
			}
		},
		watch: {
			phone() {
				this.ifsend = true
				this.check_phone()
			},
			sms(val) {
				if (val.length == 6 && this.phone.length == 11) {
					this.ifopen = false
				}
			}
		},
		methods: {
			To_xieyi() {
				uni.navigateTo({
					url: '/pages/xieyi/xieyi',
				});
			},
			To_yinshi() {
				uni.navigateTo({
					url: '/pages/zhengce/zhengce',
				});
			},
			check_phone() {

				if (this.phone.length == 11) {
					var isPhone = /^1[3-9]\d{9}$/; //手机号码
					var isMob = /^0?1[3|4|5|8][0-9]\d{8}$/; // 座机格式
					if (!isPhone.test(this.phone)) {

						Toast.fail('输入正确手机号码');
						this.ifsend = true
						return
					} else {
						this.ifsend = false
					}

				}
			},
			get_sms_captcha() {
				this.countDownT = 60;
				this.setTime();
				//下方写业务
			},
			setTime() {
				var _this = this
				if (this.countDownT == 0) {
					this.ifsend = false
					this.msg_msg = "发送验证码"
				} else {
					this.ifsend = true

					this.countDownT--;
					this.msg_msg = "重新发送(" + this.countDownT + "s)"
					setTimeout(function() {
						_this.setTime();
					}, 1000)
				}
			},

			async send_code() {
				const res = await this.$myRuquest({
					url: '/api/app/yTLittleProgram/sendVerificationCode',
					method: 'post',
					data: {
						phone: this.phone
					}
				})

				if (res.code == 200) {
					Toast.clear();
					Toast.success('已发送');
					this.get_sms_captcha()

				}else{
					Toast.fail(res.msg);
					setTimeout(function() {
						Toast.clear();
					}, 1500)
				}

			},
			
			get_wx_info() {
				var _this = this
				wx.login({
					success(res) {
						if (res.code) {
							wx.request({
								url: "https://333gps.net:9010/api/app/WeChat/GetOpenId?js_code=" + res
									.code,
								method: "POST",
								success(res) {
									wx.getUserInfo({
										success(res) {
											_this.userInfo=res.userInfo
											//console.log('ddd',_this.userInfo)
											uni.setStorageSync("userico", res.userInfo.avatarUrl)
											//uni.setStorageSync("userinfo", JSON.stringify(res.userInfo))
										}
									})
								}
							})
						} else {
							wx.showToast({
								title: '出现错误',
								icon: 'fail',
								duration: 5000
							})
							console.log('出现错误！' + res.errMsg)
						}
					}
				})
			},
			async login() {
				Toast.loading({
					duration: 0,
					message: '登录中...',
					forbidClick: true,
				});
				var _this=this
				const res = await this.$myRuquest({
					url: '/api/app/yTLittleProgram/phoneLogin',
					method: 'Post',
					header: {
						Authorization:"Bearer "+uni.getStorageSync('ifLogin'),
						"content-type": 'application/json'
					},
					data: {
						phone: this.phone,
						verificationCode: this.sms
					}
				})

				if (res.code == 200) {
					Toast.clear();
					Toast.success('已登录');
					//uni.setStorageSync('ifLogin',res.data)
					_this.get_userInfo()
					setTimeout(function() {
						uni.navigateTo({
							url: '/pages/index/index',
						})
					}, 1500)

				}else{
					Toast.clear();
					Toast.loading({
						duration: 0,
						message: res.msg,
						forbidClick: true,
					});
					setTimeout(function() {
						Toast.clear();
					}, 1500)
					//uni.removeStorageSync("ifLogin")
				}

			},
			async get_userInfo() {
				const res = await this.$myRuquest({
					url: '/api/app/yTLittleProgram/driverDetail',
					header: {
						Authorization: "Bearer " + uni.getStorageSync('ifLogin'),
						"content-type": 'application/json'
					}
				})
				if (res.code == 200) {
					uni.setStorageSync('userinfo',JSON.stringify(res.data))
			
				} else {
					
				}
			
			
			},
		},
		onLoad() {
				//
				
		},
		onShow() {
				//this.userInfo = JSON.parse(uni.getStorageSync("userinfo"))
				this.get_wx_info()
				//console.log('ddddd',this.userInfo)
		}
	}
</script>

<style scoped>
	.mian_are {
		width: auto;
		padding: 0 32rpx;

	}

	.mian_are_bg {
		background: #fff;
		border-radius: 16rpx;
		margin: 32rpx;

	}

	.text_3 {
		font-size: 12px;
		color: #2E2E30;
		text-align: center;
		padding: 15px 0;
	}

	.text_2 {
		font-size: 12px;
		color: #3E3EF7;

	}

	.title {
		font-family: 22px;
		font-weight: bold;

		color: #000;
		padding: 15px 0
	}

	.msm_line {
		display: flex;
		justify-content: space-between;
		position: relative;
	}

	.msm_line:before {
		content: '';
		height: 1px;
		width: 100%;
		position: absolute;
		top: 0px;
		left: 0px;
		background: #E6E9F5;
	}

	.msm_mt {
		padding: 15px 0;
	}
</style>
